@use 'base/variables' as v;
@use 'base/mixins' as m;

.comentarios{
    padding: v.$separacion 0;

    &__heading{
        text-align: center;
    }
    &__grid{
        @include m.tablet { 
            @include m.grid(5, 2rem)
        }
    } /*Volvemos a comentario para adaptar tamanio de cada uno*/
}