@use 'base/variables' as v;
@use 'base/mixins' as m;

.footer{
    background-color: v.$secundario;
    padding-top: v.$separacion;

    &__grid{
        @include m.tablet { 
            @include m.grid(3, 1rem);
        }
    }
    &__box{
        margin-bottom: v.$separacion;

        &:last-of-type{
            margin-bottom: 0rem;
        }
    }
    &__heading{
        color: v.$primario;
        margin: 0;
        text-align: center;
        font-weight: 800;
        
        @include m.tablet { 
            text-align: left;
        }
    }
    &__nav{
        display: flex;
        flex-direction: column;
        margin-top: 2rem;

    }
    &__link,
    &__texto{
        font-size: 2rem;
        line-height: 1.5;
        color: v.$blanco;
        text-align: center;

        @include m.tablet { 
            text-align: left;
        }
    }
    &__link:hover{
        text-decoration-line: underline;
        text-decoration-style: "solid";
        text-decoration-color: rgba(85,251,154,0.87);
        text-decoration-thickness: 1px;
    }
    &__copyright{
        background-color: v.$primario;
        margin: 0;
        padding: 2rem;
        text-align: center;
        color: v.$secundario;
        font-style: italic;
    }
}