/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Roboto", sans-serif;
  font-size: 1.6rem;
}

p {
  color: #252525;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  display: block;
}

h1, h2, h3 {
  margin: 0 0 5rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3.2rem;
}

.header {
  display: flex;
  flex-direction: column;
  padding: 5rem 0;
  align-items: center;
}
@media (min-width: 1024px) {
  .header {
    flex-direction: row;
    justify-content: space-between;
  }
}
.header__logo {
  width: 10rem;
  margin-bottom: 5rem;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  gap: 2rem;
  cursor: pointer;
  transition-property: transform;
  transition-duration: 0.3s;
}
.header__logo:hover {
  transform: scale(1.1);
}
@media (min-width: 1024px) {
  .header__logo {
    margin-bottom: 0;
  }
}
.header__logo--texto {
  text-transform: uppercase;
  font-weight: 900;
  text-shadow: rgba(85, 251, 154, 0.95) 0px 0px 8px;
}

.navegacion {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
  width: 100%;
}
@media (min-width: 1024px) {
  .navegacion {
    width: auto;
    flex-direction: row;
  }
}
.navegacion__link {
  font-size: 2rem;
  font-weight: 700;
  color: #252525;
  text-decoration: none;
  transition: transform 0.3s;
}
.navegacion__link:hover {
  transform: scale(1.1, 1.1);
  text-shadow: rgba(85, 251, 154, 0.9) 0px 0px 39px;
}
.navegacion__link.navegacion__link--registrar {
  background-color: #55FB9B;
  font-size: 2rem;
  padding: 1.2rem 3rem;
  border-radius: 1rem;
  font-weight: 700;
  display: block;
  width: 100%;
  text-align: center;
  color: #252525;
  text-decoration: none;
  transition: background-color 0.3s ease;
  border: none;
  transition: transform 0.3s;
}
.navegacion__link.navegacion__link--registrar:hover {
  cursor: pointer;
  background-color: #23fa7e;
}
@media (min-width: 768px) {
  .navegacion__link.navegacion__link--registrar {
    width: auto;
  }
}

@media (min-width: 768px) {
  .formulario__grid {
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1024px) {
  .formulario__grid {
    align-items: start;
  }
}
.formulario__contenido {
  background-color: #55FB9B;
  padding: 5rem;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}
.formulario__heading {
  color: #252525;
  text-align: center;
}
.formulario__campo {
  margin-bottom: 1rem;
}
.formulario__label {
  display: block;
  text-transform: uppercase;
  color: #252525;
  margin: 1rem 0;
}
.formulario__input {
  width: 100%;
  padding: 1.5rem;
  border: none;
  font-weight: 700;
}
.formulario__submit {
  margin-top: 2rem;
  background-color: #455a64;
  font-size: 2rem;
  padding: 1.2rem 3rem;
  border-radius: 1rem;
  font-weight: 700;
  display: block;
  width: 100%;
  text-align: center;
  color: #FFFFFF;
  text-decoration: none;
  transition: background-color 0.3s ease;
  border: none;
}
.formulario__submit:hover {
  cursor: pointer;
  background-color: #303f46;
}
@media (min-width: 768px) {
  .formulario__submit {
    width: auto;
  }
}
@media (min-width: 768px) {
  .formulario__submit {
    width: 100%;
  }
}
.formulario__imagen-contenedor {
  margin-top: 5rem;
  display: flex;
  justify-content: center;
}
.formulario__imagen {
  width: 80rem;
}
@media (min-width: 1024px) {
  .formulario__imagen {
    width: 150%;
  }
}

.pasos {
  margin-top: 2rem;
}
.pasos__contenido {
  background-color: #55FB9B;
  margin-top: -1px;
  /*para borrar el pixel que se forma*/
}
.pasos__heading {
  text-align: center;
}
.pasos__heading--descripcion {
  font-size: 1.8rem;
  display: block;
  color: #455a64;
}
@media (min-width: 768px) {
  .pasos__grid {
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.pasos__paso {
  border: 1px solid #FFFFFF;
  margin-bottom: 2rem;
  transition: transform 0.3s;
}
.pasos__paso:hover {
  -webkit-box-shadow: 0px 0px 105px 45px rgba(255, 255, 255, 0.9);
  -moz-box-shadow: 0px 0px 105px 45px rgba(255, 255, 255, 0.9);
  box-shadow: 0px 0px 105px 45px rgba(255, 255, 255, 0.9);
  transform: scale(1.1);
}
.pasos__titulo {
  text-align: center;
  font-size: 3rem;
}

.comentarios {
  padding: 5rem 0;
  /*Volvemos a comentario para adaptar tamanio de cada uno*/
}
.comentarios__heading {
  text-align: center;
}
@media (min-width: 768px) {
  .comentarios__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(5, 1fr);
  }
}

.comentario {
  background-color: #455a64;
  padding: 2rem;
  margin-bottom: 2rem;
  border-radius: 1rem;
  color: #FFFFFF;
  /*Adaptamos tamanio de las celdas de cada comentario*/
}
.comentario:last-of-type {
  margin-bottom: 0;
}
@media (min-width: 768px) {
  .comentario {
    margin-bottom: 0;
  }
  .comentario:nth-child(1) {
    grid-column: 1/4;
  }
  .comentario:nth-child(3) {
    grid-column: 5/6;
    grid-row: 1/3;
  }
  .comentario:nth-child(4) {
    grid-column: 1/3;
  }
  .comentario:nth-child(5) {
    grid-column: 3/5;
  }
}
.comentario__header {
  display: flex;
  /*disposicion de izq a drcha*/
  gap: 2rem;
  align-items: center;
  /*alineacion vertical*/
}
.comentario__imagen {
  flex-basis: 8rem;
  /*para adecuar tamanio de la img*/
}
.comentario__autor {
  color: #FFFFFF;
  font-weight: bold;
  margin: 0.5rem 0;
}
.comentario__texto {
  font-size: 2rem;
  line-height: 1.5;
  margin: 2rem 0 0 0;
}

.favoritos {
  background-color: #eceeef;
  padding: 5rem 0;
}
.favoritos__heading {
  text-align: center;
  color: #455a64;
}
@media (min-width: 768px) {
  .favoritos__grid {
    display: grid;
    gap: 4rem;
    grid-template-columns: repeat(2, 1fr);
  }
}

.favorito {
  background-color: #FFFFFF;
  padding: 4rem;
  margin-bottom: 2rem;
  -webkit-box-shadow: 0px 0px 39px 10px rgba(69, 90, 100, 0.22);
  -moz-box-shadow: 0px 0px 39px 10px rgba(69, 90, 100, 0.22);
  box-shadow: 0px 0px 39px 10px rgba(69, 90, 100, 0.22);
  border-radius: 1rem;
}
.favorito:last-of-type {
  margin-bottom: o;
}
@media (min-width: 768px) {
  .favorito {
    margin-bottom: 0;
  }
}
@media (min-width: 1024px) {
  .favorito__grid {
    display: grid;
    grid-template-columns: 1fr 2fr;
    gap: 4rem;
    align-items: center;
  }
}
.favorito__imagen {
  max-width: 15rem;
  margin: 0 auto 2rem auto;
}
.favorito__contenido {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .favorito__contenido {
    align-items: flex-start;
    /*para evitar que siga alineando items al centro*/
  }
}
.favorito__nombre {
  margin: 1rem 0;
  color: #38a265;
  font-size: 2.4rem;
}
.favorito__calificacion {
  margin-bottom: 2rem;
}
.favorito__descripcion {
  text-align: center;
  font-size: 1.6rem;
  color: #455a64;
  line-height: 1.5;
}
@media (min-width: 768px) {
  .favorito__descripcion {
    text-align: left;
  }
}

.repartidores {
  padding: 5rem 0;
}
.repartidores__heading {
  text-align: center;
  color: #455a64;
}
@media (min-width: 768px) {
  .repartidores__grid {
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.repartidores__imagen {
  /*para centrar a mano la img*/
}
@media (min-width: 768px) {
  .repartidores__imagen {
    margin-top: 5rem;
  }
}
@media (min-width: 1024px) {
  .repartidores__imagen {
    margin-top: -8rem;
  }
}
.repartidores__contenido {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: flex-start;
}
.repartidores__texto {
  line-height: 1.5;
  color: #455a64;
  padding: 0 1rem;
  text-align: center;
}
@media (min-width: 768px) {
  .repartidores__texto {
    padding: 0;
    text-align: left;
  }
}
.repartidores__enlace {
  background-color: #55FB9B;
  font-size: 2rem;
  padding: 1.2rem 3rem;
  border-radius: 1rem;
  font-weight: 700;
  display: block;
  width: 100%;
  text-align: center;
  color: #252525;
  text-decoration: none;
  transition: background-color 0.3s ease;
  border: none;
}
.repartidores__enlace:hover {
  cursor: pointer;
  background-color: #23fa7e;
}
@media (min-width: 768px) {
  .repartidores__enlace {
    width: auto;
  }
}

.descargar {
  background-color: #eceeef;
  padding: 5rem 0;
}
.descargar__grid {
  display: flex;
  flex-direction: column-reverse;
  /*cambiar el orden titulo-img para movil*/
}
@media (min-width: 768px) {
  .descargar__grid {
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.descargar__imagen {
  width: 50%;
  margin: 5rem auto 0 auto;
  /*centrar img*/
  border-radius: 2rem;
  transition-property: transform;
  transition-duration: 0.3s;
}
@media (min-width: 768px) {
  .descargar__imagen {
    margin-top: 0;
  }
}
.descargar__imagen:hover {
  -webkit-box-shadow: 0px 0px 99px 12px rgba(85, 251, 154, 0.9);
  -moz-box-shadow: 0px 0px 99px 12px rgba(85, 251, 154, 0.9);
  box-shadow: 0px 0px 99px 12px rgba(85, 251, 154, 0.9);
  transform: scale(1.1);
}

.tiendas {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.tiendas__grid {
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(2, 1fr);
}
@media (min-width: 1024px) {
  .tiendas__grid {
    gap: 5rem;
  }
}
.tiendas__heading {
  text-align: center;
  color: #455a64;
  margin: 2rem 0;
}
.tiendas__texto {
  text-align: center;
  margin-bottom: 3rem;
}
.tiendas__enlace {
  transition-property: transform;
  transition-duration: 0.3s;
}
.tiendas__enlace:hover {
  transform: scale(1.1);
}

.footer {
  background-color: #455a64;
  padding-top: 5rem;
}
@media (min-width: 768px) {
  .footer__grid {
    display: grid;
