    grid-template-columns: repeat(3, 1fr);
  }
}
.footer__box {
  margin-bottom: 5rem;
}
.footer__box:last-of-type {
  margin-bottom: 0rem;
}
.footer__heading {
  color: #55FB9B;
  margin: 0;
  text-align: center;
  font-weight: 800;
}
@media (min-width: 768px) {
  .footer__heading {
    text-align: left;
  }
}
.footer__nav {
  display: flex;
