.cuenta {
  margin: 5rem 0;
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}
.cuenta__header {
  text-align: center;
}
.cuenta__header-heading {
  margin-bottom: 2rem;
}
.cuenta__header-subheading {
  margin-bottom: 2rem;
  font-size: 1.8rem;
}
.cuenta__header-parrafo {
  margin: 0 0 2rem 0;
  font-size: 1.5rem;
}
.cuenta__contenido {
  border: 1px solid #eceeef;
  border-radius: 1rem;
  /*formulario con blur*/
  background: rgba(238, 238, 238, 0.2);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  border-radius: 10px;
  padding: 2rem;
  margin: 0 2rem;
  text-align: center;
  margin-bottom: 5rem;
}
@media (min-width: 768px) {
  .cuenta__contenido {
    text-align: left;
  }
}
.cuenta__campo {
  margin-bottom: 1rem;
}
.cuenta__campo:last-of-type {
  margin-bottom: 0;
}
.cuenta__label {
  display: block;
  text-transform: uppercase;
  color: #252525;
  margin: 1rem 0;
  font-size: 1.2rem;
  font-weight: 700;
}
@media (min-width: 768px) {
  .cuenta__label {
    font-size: 1.5rem;
  }
}
.cuenta__input {
  width: 100%;
  height: 5rem;
  padding: 1.5rem;
  margin-bottom: 2rem;
  border: 1px solid #eceeef;
}
.cuenta__submit {
  margin-top: 2rem;
  background-color: #455a64;
  font-size: 2rem;
  padding: 1.2rem 3rem;
  border-radius: 1rem;
  font-weight: 700;
  display: block;
  width: 100%;
  text-align: center;
  color: #FFFFFF;
  text-decoration: none;
  transition: background-color 0.3s ease;
  border: none;
}
.cuenta__submit:hover {
  cursor: pointer;
  background-color: #303f46;
}
@media (min-width: 768px) {
  .cuenta__submit {
    width: auto;
  }
}
@media (min-width: 768px) {
  .cuenta__submit {
    width: 100%;
  }
}
.cuenta__submit:hover {
  color: #55FB9B;
}
.cuenta .cuenta__terminos {
  margin-bottom: 2rem;
  display: flex;
}
.cuenta .cuenta__terminos-aceptar {
  flex-direction: row;
  align-items: center;
}
.cuenta .cuenta__terminos-inputaceptar {
  width: 2rem;
  height: 2rem;
  margin-right: 0.5rem;
  align-items: center;
}
.cuenta .cuenta__terminos-condiciones {
  color: #21ac5b;
  font-weight: 600;
}

.registrar {
  margin: 5rem auto;
}
.registrar__grid {
  display: flex;
  flex-direction: column-reverse;
}
@media (min-width: 768px) {
  .registrar__grid {
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1024px) {
  .registrar__grid {
    align-items: start;
  }
}
.registrar__contenido {
  padding: 2rem;
  text-align: center;
  margin: 1rem 2rem;
}
@media (min-width: 768px) {
  .registrar__contenido {
    text-align: left;
  }
}
@media (min-width: 1024px) {
  .registrar__contenido {
    margin: 2rem 6rem;
  }
}
.registrar__formulario {
  background-color: #eceeef;
  padding: 2rem;
  border-radius: 1rem;
  margin: 1rem 2rem;
}
@media (min-width: 1024px) {
  .registrar__formulario {
    margin: 2rem 6rem;
  }
}
.registrar__formname {
  text-align: center;
}
.registrar__campo {
  margin-bottom: 1rem;
}
.registrar__label {
  display: block;
  text-transform: uppercase;
  color: #252525;
  margin: 1rem 0;
  font-weight: 700;
}
.registrar__select {
  width: 100%;
  padding: 1.2rem;
  border: none;
}
.registrar__input {
  width: 100%;
  padding: 1rem;
  border: none;
}
.registrar__submit {
  margin-top: 2rem;
  background-color: #55FB9B;
  font-size: 2rem;
  padding: 1.2rem 3rem;
  border-radius: 1rem;
  font-weight: 700;
  display: block;
  width: 100%;
  text-align: center;
  color: #252525;
  text-decoration: none;
  transition: background-color 0.3s ease;
  border: none;
